.skills {
  background: linear-gradient(
    135deg,
    rgba(2, 18, 39, 1) 0%,
    rgba(3, 25, 55, 1) 100%
  );
  padding: 40px 20px;
}

.skills-title {
  color: #fff;
  font-style: bold;
  font-size: 40px;
  padding: 20px;
}

.skills-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  flex-flow: row wrap;
}

.skills-sub {
  color: #fff;
  font-style: bold;
  margin: 10px 0px;
}
.skills-left {
  text-align: right;
  margin: 0 40px;
  width: 30%;
}

.skills-right {
  text-align: left;
  margin: 0 40px;
  width: 30%;
}

@media (max-width: 768px) {
  .skills-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  .skills-left {
    text-align: center;
    margin: 10px 0px;
    width: 100%;
  }

  .skills-right {
    text-align: center;
    margin: 10px 0px;
    width: 100%;
  }
}
